
import { NavLink } from 'react-router-dom';



const Identification = () => {
    
    return (
        <div className = 'identification'>
            <div className='identification__text'>
            <p>Si vous tes adhrent  l'association, vous pourrez prochainement vous identifier. </p>
            <ul>Vous aurez alors accs  de nombreuses fonctionnalits comme:
                <li>L'accs  la bibliothque de l'association</li>
                <li>La localisation des membres</li>
                <li>La mise  disposition de documents</li>
                <li>L'accs aux petites annonces</li>
                <li>...</li>
            </ul>
            <p>En attendant, vous pouvez toujours visiter notre nouveu site!</p>
            <NavLink to='/Home' className ='button'><div>Accder au site</div></NavLink>
            <h2>Merci pour votre patience!</h2>
            </div>
        </div>
    );
};

export default Identification;