import React, { useContext, useEffect, useState } from 'react';
import AdminCreateUser from './AdminCreateUser';
import { Button, Modal } from 'antd';
import { usersContext } from '../../context/UsersContext';


const AdminUsers = () => {
    const [isModalVisible, setIsModalVisible] = useState(false);

    const { users, getAllUsers, setShowUsers, usersOfYear } = useContext(usersContext)
 
        
    useEffect(()=>{
        getAllUsers()
    },[])

    const handleCancel = () => {
        setIsModalVisible(false);
    };
    
    const showModal = (e) => {
        setIsModalVisible(true);
    };
  
    const onClick=()=>{
        setShowUsers(true)
    }
    return (
        <div>
            
            <h1>Les adhrents</h1>
            <div className='usercounter'>Il y a {usersOfYear.length} adhrents  l'association.</div>

            <div className='createUserModal'>
                <Button type="primary" onClick={showModal}>Crer un nouvel adhrent</Button>
                <Modal
               title="Crer un nouvel adhrent" 
               visible={isModalVisible} 
               destroyOnClose={true}
               onCancel={handleCancel}
                footer={[
                    <Button key="back" onClick={handleCancel}>
                        Fermer
                    </Button>,
                    ]}
                >
                    <AdminCreateUser setIsModalVisible={setIsModalVisible} />
                </Modal>
            </div>
    
    <button onClick={onClick}>Voir tous les adhrents</button>
        
        </div>
    );
};

export default AdminUsers;